.header {
  position: fixed;
  z-index: 15;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 0;
  color: #fff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 30px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 30px;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(50px);
    border-radius: 10px;
    padding: 10px 20px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 5px;
    display: flex;
    align-items: stretch;
    gap: 5px;
  }
  &__logo {
    width: 97px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 20px;
    li {
      position: relative;
      overflow: hidden;
      &:hover {
        a:nth-child(1) {
          transform: translateY(-110%);
        }
        a:nth-child(2) {
          transform: translateY(0);
        }
      }
    }
    li a {
      display: inline-block;
      font-size: 14px;
      line-height: 115%;
      letter-spacing: -0.28px;
      transition: all 0.5s;
    }
    li a:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(110%);
    }
  }
  &__group {
    height: 100%;
    display: flex;
    align-items: center;
    gap: 36px;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(50px);
    border-radius: 10px;
    padding: 10px 20px;
  }
  &__phone {
    position: relative;
    width: max-content;
    font-size: 14px;
    line-height: 115%;
    font-weight: 600;
    letter-spacing: -0.28px;
    overflow: hidden;
    &:hover {
      span:nth-child(1) {
        transform: translateY(-110%);
      }
      span:nth-child(2) {
        transform: translateY(0);
      }
    }
    span {
      display: block;
      transition: all 0.5s;
    }
    span:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(110%);
    }
  }
  &__login-wrap {
    position: relative;
    overflow: hidden;
    &:hover {
      .header__login:nth-child(1) {
        transform: translateY(-110%);
      }
      .header__login:nth-child(2) {
        transform: translateY(0);
      }
    }
  }
  &__login {
    display: flex;
    align-items: center;
    gap: 12px;
    transition: all 0.5s;
    &:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(110%);
    }
    &-icon {
      width: 12px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 115%;
      font-weight: 600;
      letter-spacing: -0.28px;
    }
  }
  .select {
    max-width: 200px;
    min-width: 200px;
    &-show {
      height: 100%;
    }
  }
  &-menu {
    display: none;
  }
}
.burger {
  display: none;
}

@media screen and (max-width: 1100px) {
  .header {
    &__inner {
      gap: 20px;
    }
    &__left {
      gap: 20px;
    }
    &__list {
      li a {
        font-size: 13px;
      }
    }
    &__group {
      gap: 20px;
    }
    &__phone {
      font-size: 13px;
    }
    &__login {
      &-text {
        font-size: 13px;
      }
    }
    .select {
      max-width: 180px;
      min-width: 180px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 15px 0;
    &__inner {
      position: relative;
      z-index: 12;
      .header__list {
        display: none;
      }
      .header__group {
        display: none;
      }
    }
    &__left {
      width: 50%;
      flex-grow: 1;
      justify-content: space-between;
      gap: 16px;
    }
    &__right {
      width: 50%;
      order: -1;
      flex-grow: 1;
    }
    .select {
      max-width: unset;
      min-width: unset;
    }
    &-menu {
      position: fixed;
      z-index: 8;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: start;
      gap: 40px;
      background: #484848;
      padding: 112px 12px 30px 12px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .header__list {
        display: inline-flex;
        flex-direction: column;
        align-items: start;
        gap: 20px;
        li a {
          transform: unset !important;
          font-size: 24px;
          letter-spacing: -1.44px;
          &:hover {
            color: #BA9166;
          }
        }
      }
      .header__phone {
        font-size: 14px;
        overflow: unset;
        &:hover {
          color: #BA9166;
        }
      }
    }
  }
  .burger {
    position: relative;
    width: 18px;
    height: 6px;
    display: block;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      background: #fff;
      transition: all 0.3s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 2px;
        transform: rotate(-35deg);
      }
      span:nth-child(2) {
        bottom: 3px;
        transform: rotate(35deg);
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 12px 0;
    &__inner {
      gap: 8px;
    }
    &__left {
      padding: 10px;
    }
    &__logo {
      width: 66px;
    }
  }
}
