.footer {
  background: #484848;
  color: #fff;
  &-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 40px 0;
    .select {
      display: none;
    }
  }
  &__logo {
    width: 133px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 30px;
    li {
      position: relative;
      overflow: hidden;
      &:hover {
        a:nth-child(1) {
          transform: translateY(-110%);
        }
        a:nth-child(2) {
          transform: translateY(0);
        }
      }
    }
    li a {
      display: inline-block;
      font-size: 16px;
      line-height: 115%;
      font-weight: 600;
      letter-spacing: -0.32px;
      transition: all 0.5s;
    }
    li a:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(110%);
    }
  }
  &-center {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 40px 0;
    .footer__list {
      display: none;
    }
  }
  &__wrap {
    max-width: 530px;
  }
  &__location {
    margin-bottom: 40px;
  }
  &__subtitle {
    font-size: 14px;
    line-height: 125%;
    font-weight: 500;
    letter-spacing: -0.56px;
    color: #969696;
    margin-bottom: 10px;
  }
  &__text {
    font-size: 16px;
    line-height: 125%;
    font-weight: 600;
    letter-spacing: -0.32px;
  }
  &__group {
    display: flex;
    align-items: start;
    gap: 60px;
  }
  &__phone,
  &__email {
    position: relative;
    display: block;
    overflow: hidden;
    &:hover {
      span:nth-child(1) {
        transform: translateY(-110%);
      }
      span:nth-child(2) {
        transform: translateY(0);
      }
    }
    span {
      display: block;
      transition: all 0.5s;
    }
    span:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(110%);
    }
  }
  &__phone {
    margin-bottom: 10px;
  }
  .select {
    max-width: 200px;
    min-width: 200px;
    flex-shrink: 0;
    &-show {
      height: 100%;
    }
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    padding: 40px 0;
  }
  &__logo2 {
    width: 157px;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &-top {
      gap: 25px;
      padding: 30px 0;
    }
    &__logo {
      width: 100px;
    }
    &__list {
      gap: 25px;
      li a {
        transform: translateY(0) !important;
        font-size: 12px;
        letter-spacing: -0.24px;
        &:hover {
          color: #BA9166;
        }
      }
      li a:nth-child(2) {
        display: none;
      }

    }
    &-center {
      gap: 25px;
      padding: 30px 0;
    }
    &__wrap {
      max-width: 440px;
    }
    &__location {
      margin-bottom: 20px;
    }
    &__subtitle {
      font-size: 12px;
      letter-spacing: -0.48px;
    }
    &__text {
      font-size: 12px;
      letter-spacing: -0.24px;
    }
    &__group {
      gap: 40px;
    }
    &__phone,
    &__email {
      &:hover {
        color: #BA9166;
      }
      span {
        transform: translateY(0) !important;
      }
      span:nth-child(2) {
        display: none;
      }
    }
    .select {
      max-width: 140px;
      min-width: 140px;
      &-show {
        min-height: 40px;
        padding: 8px 30px 8px 8px;
      }
      &-current {
        font-size: 10px;
        letter-spacing: -0.2px;
      }
      &-hidden {
        padding: 25px 8px 8px 8px;
      }
      .btn-title {
        font-size: 10px;
        letter-spacing: -0.2px;
      }
      .btn-text {
        font-size: 8px;
        letter-spacing: -0.16px;
      }
    }
    &-bottom {
      gap: 25px;
      padding: 30px 0;
    }
    &__logo2 {
      width: 100px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &-top {
      border-bottom: unset;
      padding: 20px 0 10px;
      .select {
        display: block;
      }
      .footer__list {
        display: none;
      }
    }
    &-center {
      flex-direction: column;
      align-items: start;
      padding: 10px 0 20px;
      .select {
        display: none;
      }
      .footer__list {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        padding: 20px 0 0 0;
      }
    }
    &-bottom {
      padding: 20px 0;
    }
  }
}