.wrapper {
  overflow: hidden;
}
.select {
  position: relative;
  width: 100%;
  &--active {
    z-index: 5;
    .select-current {
      color: rgba(255, 255, 255, 0.4);
    }
    .select-arrow {
      &::before {
        opacity: 1;
      }
      img {
        transform: rotate(-180deg);
      }
    }
    .select-hidden {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
  }
  &-show {
    position: relative;
    z-index: 2;
    min-height: 48px;
    background: #16467E;
    border-radius: 10px;
    padding: 10px 30px 10px 10px;
    cursor: pointer;
  }
  &-current {
    font-size: 14px;
    line-height: 100%;
    font-weight: 600;
    letter-spacing: -0.28px;
  }
  &-arrow {
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 10px;
    display: flex;
    align-items: center;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 20px;
      height: 20px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 4px;
      opacity: 0;
      transition: all 0.125s;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
      transition: all 0.3s;
    }
  }
  &-hidden {
    position: absolute;
    top: calc(100% - 15px);
    left: 0;
    transform: translateY(-8px);
    width: 100%;
    background: #16467E;
    border-radius: 0 0 10px 10px;
    padding: 30px 10px 10px 10px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
  }
  &-items {
    display: grid;
    grid-template-columns: 100%;
  }
  &-item {
    font-size: 14px;
    line-height: 100%;
    font-weight: 600;
    letter-spacing: -0.28px;
    padding: 10px 0;
    cursor: pointer;
    &:not(:last-child) {
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    }
  }
  &-item__btn {
    text-align: start;
    background: transparent;
    padding: 0;
    color: #fff;
  }
  .btn-title {
    display: block;
    font-size: 14px;
    line-height: 100%;
    font-weight: 600;
    letter-spacing: -0.28px;
  }
  .btn-text {
    font-size: 10px;
    line-height: 100%;
    font-weight: 500;
    letter-spacing: -0.2px;
    color: #BA9166;
  }
}
.btn-blue {
  background: #16467E;
  border: 1px solid #16467E;
  color: #fff;
  &:hover {
    background: transparent;
    color: #16467E;
  }
  span {
    line-height: 120%;
    font-weight: 500;
  }
}
.btn-white {
  background: #fff;
  color: #16467E;
  &:hover {
    background: #16467E;
    color: #fff;
  }
  span {
    line-height: 120%;
  }
}
.h2 {
  font-size: 64px;
  line-height: 100%;
  font-weight: 400;
  letter-spacing: -4.48px;
  sup {
    font-size: 40px;
    line-height: 100%;
    letter-spacing: -2.8px;
  }
}
.btn-black__outline {
  background: transparent;
  border: 1px solid #484848;
  color: #484848;
  &:hover {
    background: #484848;
    color: #fff;
  }
  span {
    line-height: 120%;
  }
}
.fancybox__container {
  .carousel__button {
    display: none !important;
  }
  .fancybox__button--close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 30px;
    height: 30px;
    display: block !important;
    background: #fff;
    border-radius: 6px;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 15px;
      height: 15px;
      background-image: url(../img/close.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    svg {
      display: none;
    }
  }
  .is-next,
  .is-prev {
    width: 30px;
    height: 30px;
    display: block !important;
    border: 1px solid #fff;
    border-radius: 6px;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 15px;
      height: 15px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    svg {
      display: none;
    }
  }
  .is-prev {
    left: 150px !important;
    &::after {
      background-image: url(../img/prev-icon.svg);
    }
  }
  .is-next {
    right: 150px !important;
    &::after {
      background-image: url(../img/next-icon.svg);
    }
  }
  .fancybox__counter {
    display: none;
  }
  .fancybox__thumbs {
    display: none;
  }
  .fancybox__slide {
    padding-left: 256px;
    padding-right: 256px;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(24, 24, 27, 0.92);
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 100px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 40px);
    margin: 0 auto;
  }
}
.modal--1 {
  padding: 15px 0;
  .modal__close {
    position: absolute;
    z-index: 3;
    top: 25px;
    right: 25px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 6px;
    svg {
      max-width: 60px;
    }
  }
  .travel {
    padding: 0 !important;
    .item-hidden {
      display: block !important;
    }
  }
  .item {
    background: #fff;
    padding: 15px 12px !important;
    &-show {
      cursor: auto;
    }
    &__btn2 {
      display: none !important;
    }
    &-hidden {
      width: calc(100% + 24px);
      margin-left: -12px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .h2 {
    font-size: 48px;
    letter-spacing: -3.36px;
    sup {
      font-size: 30px;
      letter-spacing: -2.1px;
    }
  }
  .fancybox__container {
    .is-prev {
      left: 40px !important;
    }
    .is-next {
      right: 40px !important;
    }
    .fancybox__slide {
      padding-left: 100px !important;
      padding-right: 100px !important;
    }
  }
}

@media screen and (max-width: 1100px) {
  .select {
    &-current {
      font-size: 13px;
    }
    &-item {
      font-size: 13px;
    }
    .btn-title {
      font-size: 13px;
    }
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 40px;
    letter-spacing: -2.8px;
    sup {
      font-size: 20px;
      letter-spacing: -1.4px;
    }
  }
  .fancybox__container {
    .fancybox__button--close {
      top: 30px;
      right: 50%;
      transform: translate(50%, 0);
    }
    .is-prev {
      left: 20px !important;
    }
    .is-next {
      right: 20px !important;
    }
    .fancybox__slide {
      padding: 90px 70px !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .select {
    &-show {
      min-height: 40px;
      padding: 8px 26px 8px 8px;
    }
    &-current {
      font-size: 10px;
      letter-spacing: -0.2px;
    }
    &-item {
      font-size: 10px;
      letter-spacing: -0.2px;
    }
    .btn-title {
      font-size: 10px;
      letter-spacing: -0.2px;
    }
    .btn-text {
      font-size: 8px;
      letter-spacing: -0.16px;
    }
    &-hidden {
      padding-top: 20px;
    }
  }
  .fancybox__container {
    .is-prev {
      top: unset !important;
      left: calc(50% - 20px) !important;
      bottom: 30px !important;
      transform: translate(-50%, 0);
    }
    .is-next {
      top: unset !important;
      right: calc(50% - 20px) !important;
      bottom: 30px !important;
      transform: translate(50%, 0);
    }
    .fancybox__slide {
      padding: 90px 12px !important;
    }
  }
}