.main {
  position: relative;
  background-image: url(../img/main-img.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 100px 0 50px;
  color: #fff;
  &__text {
    max-width: 360px;
    font-size: 16px;
    line-height: 125%;
    letter-spacing: -0.32px;
    color: #BA9166;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 20px;
    margin-top: 245px;
  }
  &__title-wrap {
    position: relative;
  }
  &__title {
    font-size: 96px;
    line-height: 88%;
    font-weight: 400;
    letter-spacing: -6.72px;
    span {
      color: #BA9166;
    }
  }
  &__title-img {
    position: absolute;
    top: 95px;
    left: 342px;
    width: 130px;
    img {
      width: 100%;
    }
  }
  &__group {
    flex-shrink: 0;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 4px / 2));
    gap: 4px;
    width: 422px;
  }
  .select {
    &--active {
      z-index: 6;
    }
    &-show {
      min-height: 56px;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      background: rgba(0, 0, 0, 0.4);
      backdrop-filter: blur(40px);
      padding: 12px 14px;
    }
    &-title {
      font-size: 10px;
      line-height: 100%;
      font-weight: 500;
      letter-spacing: -0.2px;
      color: #B5B5B5;
      margin-bottom: 5px;
    }
    &-current {
      font-size: 12px;
      font-weight: 500;
      letter-spacing: -0.48px;
    }
    &-arrow {
      position: relative;
      right: unset;
      bottom: unset;
      flex-shrink: 0;
    }
    &-hidden {
      background: rgba(0, 0, 0, 0.4);
      backdrop-filter: blur(40px);
      padding-top: 20px;
    }
    &-item {
      font-size: 12px;
      letter-spacing: -0.24px;
    }
  }
  .select:nth-child(1) {
    grid-column: 1/3;
  }
  &__btn {
    min-height: 56px;
    flex-direction: column;
    align-items: start;
    border-radius: 8px;
    padding: 5px 10px;
    &:hover {
      background: rgba(22, 70, 126, 0.8);
      color: #fff;
    }
    .btn-title {
      font-size: 14px;
      font-weight: 600;
      letter-spacing: -0.28px;
    }
    .btn-text {
      font-size: 10px;
      font-weight: 500;
      letter-spacing: -0.2px;
      color: #BA9166;
    }
  }
  .calendar {
    position: relative;
    min-height: 56px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    border-radius: 10px;
    padding: 12px 14px;
    cursor: pointer;
    &::before {
      content: '';
      position: absolute;
      z-index: 4;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
      backdrop-filter: blur(40px);
      border-radius: 10px;
      pointer-events: none;
    }
    label {
      position: relative;
      z-index: 5;
      font-size: 10px;
      line-height: 100%;
      font-weight: 500;
      letter-spacing: -0.2px;
      color: #B5B5B5;
      margin-bottom: 5px;
      cursor: pointer;
    }
    input {
      position: relative;
      z-index: 5;
      background: transparent;
      border: none;
      font-size: 12px;
      line-height: 100%;
      font-weight: 500;
      letter-spacing: -0.48px;
      color: #fff !important;
      margin: 0;
      pointer-events: none;
      &::placeholder {
        color: #fff !important;
      }
    }
    .air-datepicker {
      position: absolute;
      z-index: 3;
      top: 100%;
      left: 0;
      transform: translateY(-8px);
      width: 100%;
      background: transparent !important;
      border: none !important;
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
      &::before {
        content: '';
        position: absolute;
        z-index: -1;
        top: -20px;
        left: 0;
        width: 100%;
        height: calc(100% + 20px);
        background: rgba(0, 0, 0, 0.4) !important;
        border-radius: 0 0 10px 10px;
        -webkit-backdrop-filter: blur(40px) !important;
        backdrop-filter: blur(40px) !important;
        pointer-events: none;
      }
    }
    .air-datepicker .datepicker--content,
    .air-datepicker .air-datepicker--content,
    .air-datepicker .air-datepicker__content {
      background: transparent !important;
      border: none !important;
    }
    .air-datepicker-nav {
      border-bottom: 1px solid rgba(255, 255, 255, 0.1) !important;
    }
    .air-datepicker-nav--title {
      color: #fff;
      &:hover {
        background: transparent !important;
      }
    }
    .air-datepicker-nav--action {
      &:hover {
        background: transparent !important;
      }
    }
    .air-datepicker-cell {
      color: rgba(255, 255, 255, 0.5) !important;
      &:hover {
        background: transparent !important;
      }
    }
    .-other-month- {
      color: #fff !important;
    }
  }
  .calendar--active {
    .air-datepicker {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
  }
}
.info {
  background: #E9E9E9;
  padding: 120px 0;
  &__inner {
    max-width: 965px;
    margin-left: auto;
  }
  &__title {
    max-width: 930px;
    font-size: 48px;
    line-height: 105%;
    font-weight: 400;
    letter-spacing: -2.88px;
    color: #88714C; 
    span {
      color: #16467E;
    }
  }
  &__wrap {
    margin-top: 45px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 60px;
    background: #fff;
    border-radius: 10px;
    padding: 20px;
    &-number {
      font-size: 128px;
      line-height: 90%;
      letter-spacing: -11.52px;
      color: #16467E;
    }
    &-text {
      max-width: 250px;
      text-align: right;
      font-size: 20px;
      line-height: 120%;
      letter-spacing: -0.4px;
      color: #969696;
      margin-left: auto;
    }
