}
.video-section {
  position: relative;
  .video {
    position: relative;
    width: 100%;
    min-height: 768px;
    padding-bottom: 56%;
    video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .video__logo {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 160px;
    pointer-events: none;
    img {
      width: 100%;
    }  
  }
  .video__play {
    position: absolute;
    z-index: 2;
    right: 40px;
    bottom: 30px;
    width: 40px;
    cursor: pointer;
    transition: all 0.125s;
    img {
      width: 100%;
    }
  }
  .video__close {
    position: absolute;
    top: 30px;
    right: 40px;
    width: 32px;
    height: 12px;
    display: none;
    cursor: pointer;
    pointer-events: none;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #fff;
      transition: all 0.3s;
    }
    span:nth-child(1) {
      top: 4px;
      transform: rotate(-35deg);
    }
    span:nth-child(2) {
      bottom: 5px;
      transform: rotate(35deg);
    }
  }
}
.video-section--active {
  .video__logo {
    display: none;
  }
  .video__play {
    display: none;
  }
  .video__close {
    display: block;
    opacity: 1;
    pointer-events: auto;
  }
}
